import React from 'react';
import { connect } from 'react-redux';
import placeholderImg from "../css/square-image.png";
import { updateItinerary,removeItinerary } from '../actions/Itinerary'


class Itinerary extends React.Component {
    state = {
            location: '',
            description: ''
    }
    handleChange = (e) => {
        let {location, description} = this.state;
        switch(e.target.name) {
            case 'location':
            location = e.target.value;
            this.setState({
                location
            });            
            break;
            case 'description':
                description = e.target.value;
                this.setState({
                    description
                })            
            break;
            default:
                console.log("default of handleChange on:",this.props.id)           
        }
        let data = {
            location,
            description
        }
        this.props.updateItinerary(this.props.id,data)
        console.log('onChange',this.props.id,data)
    }

    handleRemove = () => {
        this.props.removeItinerary(this.props.id)
    }
    render() {
        let {location,description} = this.state;
        let  {dayCount} = this.props;
        return <div className="cell medium-8">
            <div className="grid-x grid-margin-x itinerary_container">
              <span className="day">
                Day {dayCount}
              </span>
              <div className="remove_btn" onClick={this.handleRemove}>
                <span className="close icon" />
              </div>

              <div className="cell medium-6">
                <img src={placeholderImg} className="intro_img" />
              </div>
              <div className="cell medium-6 text-left">
                <label>
                  <input type="text" placeholder="Location" name="location" value={location} onChange={this.handleChange} />
                  <textarea placeholder="Description" name="description" value={description} onChange={this.handleChange} />
                </label>
              </div>
            </div>
          </div>;
    }
}
export default connect(null,{updateItinerary, removeItinerary})(Itinerary);


