import React, { Component } from 'react';
import logo from '../css/logo.png';
import placeholderImg from "../css/square-image.png";
// import bannerImg from '../css/celebrate-6-seasons.jpg';
import {connect} from 'react-redux'
import {
  addItinerary
} from '../actions/Itinerary.js'
import '../css/App.css';
import Select from "react-select";
import Itinerary from './Itinerary';

var bestTimeOptions = [
  { value: "Basanta -spring", label: "Basanta -spring" },
  { value: "Grishma- summer", label: "Grishma- summer" },
  { value: "Varsha-monsoon", label: "Varsha-monsoon" },
  { value: "Sharada-autumn", label: "Sharada-autumn" },
  { value: "Hemanta- pre winter", label: "Hemanta- pre winter" },  
  { value: "Shishir- winter", label: "Shishir- winter" },
];
var categoryOptions = [
  { value: "Event", label: "Event" },
  { value: "Festival", label: "Festival" },
  { value: "Immersive", label: "Immersive" },
  { value: "Outdoors", label: "Outdoors" },
  { value: "Discovery", label: "Discovery" },
  { value: "Adventure", label: "Adventure" }
];
var durationOptions = [
  { value: "Whole morining", label: "Whole moring" },
  { value: "A whole day", label: "A whole day" },
  { value: "1 day -1 night", label: "1 day - 1 night" },
  { value: "2 days - 1 night", label: "2 days - 1 night" }
];
class App extends Component {
  state = {
    category: { value: "Outdoors", label: "Outdoors" },
    duration: { value: "Whole morining", label: "Whole moring" },
    bestTime: { value: "Hemanta- pre winter", label: "Hemanta- pre winter" },
    itinerary: 1
  };
  onBestTimeChange = val => {
    console.log("Selected: " + JSON.stringify(val));
    this.setState({ bestTime: val });
  };
  onDurationChange = val => {
    console.log("Selected: " + JSON.stringify(val));
    this.setState({
      duration: val
    });
  };
  onCategoryChange = val => {
    console.log("Selected: " + JSON.stringify(val));
    this.setState({
      category: val
    });
  };
  addItinerary = () => {
    let id = this.state.itinerary + 1;
    this.props.addItinerary(id);
    this.setState({
      itinerary: id
    })
  }
  renderItinerary = () => {
    if(this.props.itinerary.byId.length !== 0) {
      return this.props.itinerary.byId.map((obj, i) => {
        return <Itinerary key={i} id={obj} dayCount={i + 1} />;
      });
    } else {
      return <div className="itinerary_container cell medium-8">
          Itineraries are very important.Please fill them now if you can.<br />
          But you can also come back and edit them later.
        </div>;
    }
  }
  render() {
    return <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Celebrate Six Seasons of Nepal</h2>
          <p className="App-intro">
            Create A Package to celebrate a season.
          </p>
        </div>
        <div className="grid-container fluid">
          <div className="grid-x grid-margin-x grid-margin-y section theOverview ">
            <div className="cell medium-5 text-right">
              <img src={placeholderImg} className="intro_img" />
            </div>
            <div className="cell medium-6">
              <h1 className="theTitle"> The Overview</h1>
              <div className="grid grid-margin-x text-left">
                <div className="cell">
                  <label>
                    Title
                    <input type="text" placeholder="The Title Of the Package" />
                  </label>
                </div>
              </div>
              <div className="grid-x grid-margin-x text-left">
                <div className="cell medium-6">
                  <label>
                    Area
                    <input type="text" placeholder="From and to" />
                  </label>
                </div>
                <div className="cell medium-6">
                  <label>
                    Best Time
                    <Select name="Duaration" placeholder="Time of Year" value={this.state.bestTime} options={bestTimeOptions} onChange={this.onBestTimeChange} />
                  </label>
                </div>
              </div>
              <div className="grid-x grid-margin-x text-left">
                <div className="cell medium-6">
                  <label>
                    Duration
                    <Select.Creatable name="Duaration" value={this.state.duration} options={durationOptions} onChange={this.onDurationChange} />
                  </label>
                </div>
                <div className="cell medium-6">
                  <label>Category</label>
                  <Select.Creatable name="Category" value={this.state.category} options={categoryOptions} multi onChange={this.onCategoryChange} />
                </div>
              </div>
            </div>
          </div>

          <div className="grid-x grid-margin-x grid-padding-x grid-margin-y grid-padding-y align-center section theDescription">
            <div className="cell medium-8 text-center">
              <h1 className="theTitle"> The Description</h1>
            </div>

            <div className="cell medium-8">
              <label>
                Give a short description of the package.
                <textarea placeholder="Tell us about the package in 50 words" />
              </label>
            </div>
            <div className="cell medium-8">
              <label>
                Give a Longer description of the package.
                <textarea placeholder=" in 200 words" style={{ height: "500px" }} />
              </label>
            </div>
          </div>

          <div className="grid-x grid-margin-x grid-padding-x grid-margin-y grid-padding-y align-center section theDescription">
            <div style={{ position: "relative" }} className="cell medium-8 text-left">
              <h1 className="theTitle"> The Itinerary</h1>
              <span className="add_btn" onClick={() => this.addItinerary()}>
                Add a day
              </span>
            </div>

            {this.renderItinerary()}
          </div>
        </div>
      </div>;
  }
}

const mapStateToProps = (state) => {
  return {
   itinerary: state.itinerary 
  }
}
export default connect(mapStateToProps,{ addItinerary })(App);



