import  {
    ADD_ITINERARY,
    UPDATE_ITINERARY,
    REMOVE_ITINERARY,
} from './Types.js'

export const addItinerary = (id) => {
    return {
        type: ADD_ITINERARY,
        id,
        payload: {
            id,
            content: ''
        }
    }
}
export const updateItinerary = (id,data) => {
    return {
        type: UPDATE_ITINERARY,
        id,
        payload: {
            id,
            content: data
        }
    }
}
export const removeItinerary = (id) => {
    return {
        type: REMOVE_ITINERARY,
        id
    }
}


